.app-wrapper {
    display: grid;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 5px;
    grid-template-areas:
    'h h h'
    'n c c'
    'f f f';
    grid-template-rows: 100px 1fr 50px;

    width: auto;
    height: 100vh;
    margin: 0 auto;
}

.app-wrapper-content {
    grid-area: c;

    background-color: rgba(143, 202, 239, 0.41);
}
.app-wrapper-initial-progress {
    position: absolute;
    top: 0;
    left: 0;

    overflow: auto;
    grid-area: c;

    width: 100%;
    height: 100%;

}
.app-initial-progress {
    position: absolute;
    top: 50%;
    left: 50%;

    grid-area: c;

    margin:auto;
}

.footer {
    grid-area: f;

    text-align: center;

    background:#6cf;

    /*margin: 0 auto 40px;*/
}
